import React from 'react'
import { Field, reduxForm } from 'redux-form'
import { maxLengthCreator, requiredField } from '../../../utils/validators/validators'
import { Element } from '../../FormControls/FormsControls'
import s from './LoginForm.module.css'

const maxLength30 = maxLengthCreator(30)
const input = Element("input")

const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field name={"email"} type="text" placeholder="Login" component={input} validate={[requiredField, maxLength30]} />
            </div>
            <div>
                <Field name={"password"} type="password" placeholder="Password" component={input}  validate={[requiredField, maxLength30]}/>
            </div>
            <div>
                <Field name={"rememberMe"} type="checkbox" component={input} />
                <span>remember me</span>
            </div>
            {props.error && <div className={s.form__error}>{props.error}</div>}
            <div>
                <button type="submit">Submit</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({form: "login"})(LoginForm)

export default LoginReduxForm