import React from 'react'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { loginThunkCreator, logoutThunkCreator } from '../../redux/authReducer'
import { getIsAuth } from '../../redux/authSelectors'
import Login from './Login'


class LoginComponent extends React.Component {

    onLogin = (formData) => {
        let {email, password, rememberMe} = formData

        this.props.loginThunk(email, password, rememberMe)
    }


    render() {
        return <Login onLogin={this.onLogin} isAuth={this.props.isAuth} />
    }
}

let mapStateToProps = (state) => {
    return {
        isAuth: getIsAuth(state)
    }
}

export default compose(
    connect(mapStateToProps, {
    loginThunk: loginThunkCreator,
    logoutThunk: logoutThunkCreator
})
)(LoginComponent)
